/* ==========================================================================
   Portfolio Single - Slider layout style - begin
   ========================================================================== */

.eltdf-portfolio-single-holder {
	
	&.eltdf-ps-slider-layout {
		
		.eltdf-ps-image-holder {
			margin: 0 0 65px;
			
			.eltdf-ps-image-inner {
				visibility: hidden;
			}
			
			.eltdf-ps-image {
				
				img {
					width: 100%;
				}
			}
		}

		.eltdf-ps-info-holder{
			padding: 45px 40px 104px 40px;
			box-sizing: border-box;
			background-color: #f7f8f9;
		}

		.eltdf-grid-col-3{
			width: 27%;

			@media only screen and (max-width: 1024px){
				width: 100%;
				margin: 0;
			}
		}

		.eltdf-grid-col-9{
			width: 71%;
			margin-left: 2%;

			@media only screen and (max-width: 1024px){
				width: 100%;
				margin: 0;
			}
		}
	}
}
/* ==========================================================================
   Portfolio Single - Slider layout style - end
   ========================================================================== */